.description {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: 20px; }
  .description .description__image {
    flex-basis: 50%; }
  .description__text {
    flex-basis: 50%;
    padding-right: 45px;
    margin-bottom: 24px; }
  .description__link {
    border: 2px solid rgba(0, 0, 0, 0);
    position: relative;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: #1B1C21;
    padding-left: 16px; }
    .description__link:hover, .description__link:focus {
      border: 2px solid #B8FFEC;
      border-radius: 6px; }
    .description__link:before {
      content: "";
      height: 11px;
      width: 8px;
      left: 0;
      top: 6px;
      position: absolute;
      background-image: url(./static/arrow.svg); }
  .description__p {
    max-height: 90px;
    overflow: hidden;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 0.4px;
    color: #1B1C21; }

.description::before {
  content: "";
  height: 1px;
  width: 100vw;
  position: absolute;
  background-color: #D9FFF5;
  bottom: -20px;
  left: 0px; }
