.request {
  position: relative; }
  .request .request__list {
    display: flex;
    justify-content: space-between; }
    .request .request__list .request__repair {
      background-image: url(./static/repair.svg);
      background-repeat: no-repeat;
      background-position: center; }
    .request .request__list .request__status {
      background-image: url(./static/checkstatus.svg);
      background-repeat: no-repeat;
      background-position: center; }
  .request .request__repair {
    margin-right: 16px; }
  .request .request::before {
    top: 25px; }
  .request .request__item {
    display: flex; }
  .request .request__span {
    font-family: "TTLakes-Light", Arial, serif;
    font-size: 16px;
    line-height: 40px;
    letter-spacing: -0.2px;
    color: #1B1C21;
    margin-right: 8px; }

.request::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 32px;
  top: 4px;
  left: -20px;
  background-color: #EAEAEA; }

@media (max-width: 768px) {
  .request::before {
    left: -12px; } }
