.button-order {
  position: absolute;
  bottom: 16px;
  right: 16px;
  width: 119px;
  height: 32px;
  background-color: #FF3E79;
  border-radius: 16px;
  font-size: 12px;
  line-height: 24px;
  text-align: left;
  letter-spacing: -0.15px;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 4px 40px 4px 12px;
  transition: all 0.3s; }
  .button-order::after {
    content: "";
    width: 10px;
    height: 10px;
    right: 20px;
    top: 10px;
    border-top: 2px solid #FFFFFF;
    border-right: 2px solid #FFFFFF;
    position: absolute;
    transform: rotate(45deg); }
  .button-order:hover {
    box-shadow: 0px 0px 5px 0 #1B1C21; }
  .button-order:active {
    background-color: #7E7E82; }
