.contacts {
  position: relative; }
  .contacts .contacts__list {
    display: flex;
    margin-right: 42px; }
    .contacts .contacts__list .contacts__call {
      background-image: url(./static/call.svg);
      background-repeat: no-repeat;
      background-position: center; }
    .contacts .contacts__list .contacts__chat {
      background-image: url(./static/chat.svg);
      background-repeat: no-repeat;
      background-position: center; }
    .contacts .contacts__list .contacts__profile {
      background-image: url(./static/profile.svg);
      background-repeat: no-repeat;
      background-position: center; }
    .contacts .contacts__list .contacts__item {
      margin-right: 16px; }
    .contacts .contacts__list .contacts__item:last-child {
      margin-right: 0px; }

@media (max-width: 768px) {
  .header__contacts {
    display: none; } }
