@font-face {
  font-family: TTLakes-Bold;
  src: url(./fonts/TTLakes-Bold.ttf); }

@font-face {
  font-family: TTLakes-Medium;
  src: url(./fonts/TTLakesCompressed-Medium.ttf); }

@font-face {
  font-family: TTLakes-Light;
  src: url(./fonts/TTLakesCondensed-Light.ttf); }

.body {
  max-width: 1440px;
  flex-direction: row-reverse;
  margin: 0 auto;
  position: relative;
  font-family: "TTLakes-Light", Arial, serif;
  transition: all 0.3s;
  overflow-x: hidden; }

.container {
  background-color: #F8F8F8;
  background-size: 100%; }

.main {
  padding: 16px 24px 0 24px; }

@media (min-width: 1440px) {
  .body {
    display: flex;
    padding: 42px 0 0 0; } }

.swiper {
  width: 600px;
  height: 300px; }

button:hover {
  box-shadow: 0px 0px 5px 0 #1B1C21; }

button:active {
  background-color: #7E7E82; }
